@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
.boxing {
    background: #fff;
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px 10px 20px;
    border-radius: 10px;
}

.boxing > * {
    padding: 5px 0;
}

.btn-primary {
    background: #1833ffff;
}

.btn-validation {
    background: #2CFF7B;
}

.ingredients p {
    margin: 10px;
}

.infos:hover + .ingredientContribution {
    visibility: visible;
}

.moldIngredients {
    text-align: center;
    border: 1px solid #1833ffff;
    border-radius: 5px;
    padding: 4px;
    width: 200px
}

.ingredientContribution {
    visibility: hidden;
    width: 250px;
    background: #fff;
    padding: 5px;
    z-index: 1;
    top: 20px;
    left: 0;
    position: absolute;
    font-size: 10px;
    transition: all .5s;
}

.btn-warning {
    background: #FFA922;
}

.btn-danger {
    background: #FF1227;
}

.btn {
    border: none;
    cursor: pointer;
    border-radius: 4px;
    font-size: 12px;
    margin: 4px;
    padding: 5px;
    color: #fff;
}

.heading {
    margin: 10px 10px 0px;
    font-weight: bold;
    font-size: 18px;
    position: relative;
    border-bottom: 1px solid #cac9d0;
}

.headingDecoration {
    background: #1833ffff;
    width: 39px;
    height: 10px;
    position: absolute;
    left: 0;
    bottom: 0px;
}
body {
    background: #e7e6eb;
    color: #000;
    margin: 0;
    font-family: 'Roboto', sans-serif;
    height: 100vh;
}

.important{
    font-weight: bold;
    color: #1833ffff;
}

.alignLeft{
    text-align: left;
}

.list {
    border-bottom: 1px solid #cac9d0;
}

.container {
    width: 80%;
    margin-top: 10px;
}

.flex {
    display: flex;
    justify-content: flex-start;
}

.evenly {
    justify-content: space-evenly;
}

.flexRow {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.flexColumn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.marginBetweenChildren > * {
    margin: 0 8px;
}

.btn.big {
    font-size: 20px;
}

.block {
    display: block;
    margin: 6px;
}


.logo {
    width: 120px;
}

.passwordForgot {
    text-decoration: underline;
    font-size: 10px;
    cursor: pointer;
}

table {
    width: 100%;
    background: #fff;
}

tr {
    text-align: center;
}

td, th {
    border-bottom: 1px solid #ccc;
    padding: 4px;
}


select, input, textarea {
    width: 100%;
    padding: 2px;
    margin: 5px 0;
    display: block;
    border: 2px solid #1833ffff;
    border-radius: 3px;
}

.inner {
    transition: all 1s;
    position: fixed;
    z-index: 550;
    transform: translateY(-100%);
    left: 50%;
    width: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.inner.active {
    transform: translateY(-50%) translateX(-50%);
    top: 50%;
}

.header {
    line-height: 30px;
    height: 30px;
    background: #1833ffff;
    color: #fff;
    width: 100%;
    font-size: 16px;
    font-weight: bold;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    padding: 15px;
}

.content {
    z-index: 1;
    line-height: 1;
    width: 100%;
    background: #fff;
    min-height: 100px;
    padding: 15px
}

.overlay {
    position: fixed;
    z-index: 500;
    top: 0;
    bottom: 0;
    left: 0;
    margin: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.6);
    cursor: pointer;
    padding: 0;
    display: none;
    width: 100%;
    border: 0;
}

.overlay.active {
    display: block;
}


button {
    cursor: pointer
}

@media (max-width: 550px) {
    .inner {
        width: 90%;
    }

}
.home {
    position: relative;
    margin: 0 auto;
    width: 100%;
    text-align: center;
    top: 26vw;
    transform: translateY(-45%);
}


.moldForm{
    height: auto;
}
#menu {
    height: 100vh;
    position: static;
    text-align: center;
    padding: 8px;
    overflow: hidden;
    width: 205px;
    left: 0;
}

#menu.active {
    width: 200px;
    transform: translateX(0%);
}

.avatar {
    width: 90px;
    height: 90px;
    margin: 0 auto;
    border-radius: 50%;
    overflow: hidden;
}

.avatar img {
    position: relative;
    width: 100%
}

#menu, .menu-box {
    z-index: 11;
    background: #fff;
    transition: all 1s;
}

.menu-box {
    position: relative;
    top: 48px;
    width: 100%;
}

.menu-box nav ul {
    margin: 50px 0;
    padding: 0 0 0 20px;
}

.menu-box nav ul li {
    /*	display: inline-block;*/
    display: flex;
    align-items: center;
    list-style: none;
    text-align: center;
    width: 100%;
    margin: 25px 0px;
    font-weight: bold;
}

.menu-box nav ul li > * {
    margin: 0 5px;
}

.menu-box nav li a {
    position: relative;
    display: block;
    text-align: left;
    outline: none;
    color: #4D4C51;
    text-decoration: none;
    font-weight: 400;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
    font-size: 18px;
}

.menu-box nav a:hover,
.menu-box nav a:focus {
    outline: none;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

